* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
}


.Maincontainer {
  background-image: url("../../public/media/b1.jpeg");

  height: 98vh;
  width: 100%;
  background-position: center;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  border-top: 10px solid rgb(78, 73, 73);
  cursor: pointer;
}

.Header {
  height: 100px;
  width: 100%;
  display: flex;
  /* flex-wrap: wrap; */
  justify-content: space-between;
  text-align: center;
  align-items: center;
  padding-right: 100px;
  position: relative;
}
.header_opacity{
position: absolute;
top: 0;
height: 100%;
width: 100%;
opacity:0.2;
background-color: black;
}

#imglogo {
  height: 40px;

  margin-left: 100px;
}

.Signin {
  background-color: rgb(217, 17, 17);
  color: white;
  width: 80px;
  height: 35px;
  font-size: 15px;
  border-radius: 6px;
}

.English {
  background-color: rgb(62, 46, 46);
  color: white;
  width: 140px;
  height: 35px;
  padding-left: 15px;
  font-size: 15px;
  border-radius: 6px;
  margin: 10px;
}

.English:hover {
  border-bottom: none;
  color: rgb(59, 59, 131);
  border-bottom: 1px solid black;
}

.Container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 30px;
  padding-top: 90px;
  margin-bottom: 40px;
  margin-top: 100px;
  flex-direction: column;
}
.Container h1 {
  font-size: 50px;
  font-weight: 600px;
}
.Container p {
  font-size: 25px;
  font-weight: 600px;
}

input {
  height: 50px;
  width: 400px;
  background-color: rgba(28, 25, 25, 0.825);
  padding-left: 10px;
  color: white;
  border: 1px solid whitesmoke;
  margin: 30px 0;

  border-radius: 4px;

  font-size: 20;
}

.Submit {
  padding: 20px 40px;
  background-color: red;
  color: white;
  border-radius: 4px;
  border: none;
  font-size: 1rem;
  margin-left: 10px;
}
::placeholder {
  color: white;
  font-size: 15px;
  text-align: left;
}



