.faq {
  height: auto;
  width: 100%;
  background-color: black;
  border-top: 10px solid rgb(78, 73, 73);
  color: whitesmoke;
}
.faq p {
  font-size: 25px;
  padding-left: 30px;
}
.faq i {
  font-size: 40px;
  padding-right: 30px;
  color:white;
}
.qall {
  height: 680px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 30px;
}

#faqtext {
  height: 100px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 50px;
  padding-top: 30px;
}
.q1 {
  height: 100px;
  width: 77%;
  background-color: rgb(60, 57, 57);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  margin: 5px 0;
  cursor: pointer;

}
.q1:hover {
  background-color: rgb(119, 119, 119);

}


.q1.expanded {
  background-color: grey;
  color: black;
  height: auto;
  transition: all 0.6s;
  text-align: justify;
}

.answer {
  padding: 10px 50px;
  border-top: 1px solid #ddd;
  font-size: 0.6rem;
}

.fa-plus:before {
  content: '+';
}

.fa-minus:before {
  content: '-';
}

.faqemail {
  height: 200px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.Emailsignup input {
  height: 58px;
  width: 400px;
  background-color: rgba(28, 25, 25, 0.825);
  padding-left: 10px;
  color: white;
  border: 1px solid whitesmoke;
  border-radius: 4px;
  font-size: 20px;
}

